.text-success{
  font-size: 25px !important;
  color: #3569A5 !important;
}

.badge-info {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom: transparent !important;
}

.btn{
  margin: 1px;
  border-radius: 0;
}

.option{
  font-size:20px;
  padding:10px;
  margin:3px;
}
#test .options input[type=checkbox] {
  height:22px;
  width:22px;
}

.option-img {
  max-width: 90px;
  width: 100%;
  @media only screen and (max-width: 768px) {
    max-width: 50px;
  }

}


.cursor-pointer{
  cursor: pointer;
}

.result-question{
  background-color:#eee;
  margin:4px;
  padding:6px;
}

.question-container{
border-radius: 20px;
-webkit-border-radius: 20px;
-moz-border-radius: 20px;
-ms-border-radius: 20px;
-o-border-radius: 20px;
background-color: #ffff;
}
.option {
max-height: 120px;
height: 120px;
border: 1px solid #ccc;
border-radius: 4px;
margin: 10px;
cursor: pointer;
transition: background-color 0.3s ease;
  &:hover, &:hover > *{
    cursor: pointer;
  }
}

.option-checked {
  background-color: #add8e6; /* Light blue */
  border-radius: 4px;
}

/* Optional: Additional styling for the label */
.option-label {
  display: flex;
  justify-content: center;
  align-items: center;
}

.option-letter {
display: inline-block;
width: 20px;
height: 20px;
line-height: 20px;
text-align: center;
}

input[type="radio"] {
display: none;
}



.question-title-container{
  border-bottom: 2px solid #cecece;
}

.file {
  width: 80px;
  height: auto;
  margin: 20px;

  @media only screen and (max-width: 768px) {
    width: 20px;
  }

  * {
    transform-origin: center;
  }

  &:last-child {
    margin-right: 120px;
  }
}

.file-body {
  fill: #d7d7d7;
}

.file-bend {
  fill: #848484;
}

.file-check {
  fill: #53a21e;
}

.file-loading {
  fill: none;
  stroke: #848484;
  stroke-width: 3px;
  stroke-dasharray: 49;
  stroke-linecap: round;
  transform: rotate(-90deg);
}

.move {
  animation: move 2.5s infinite;
}

.load {
  animation: load 2.5s infinite;
}

.done {
  animation: done 2.5s infinite;
}

.fadein {
  animation: fadein 2.5s infinite;
}

.fadeout {
  animation: fadeout 2.5s infinite;
}

.zoomin {
  animation: zoomin 2.5s infinite;
}

.zoomout {
  animation: zoomout 2.5s infinite;
}

@keyframes move {
  0% {
    transform: translateX(0);
  }
  60% {
    transform: translateX(0);
  }
  90% {
    transform: translateX(124px); /* Adjust translateX value as needed */
  }
  100% {
    transform: translateX(124px); /* Adjust translateX value as needed */
  }
}

@media only screen and (max-width: 768px){
  @keyframes move {
    0% {
      transform: translateX(0);
    }
    60% {
      transform: translateX(0);
    }
    90% {
      transform: translateX(80px);
      -webkit-transform: translateX(80px);
      -moz-transform: translateX(80px);
      -ms-transform: translateX(80px);
      -o-transform: translateX(80px);
}
    100% {
      transform: translateX(80px);
      -webkit-transform: translateX(80px);
      -moz-transform: translateX(80px);
      -ms-transform: translateX(80px);
      -o-transform: translateX(80px);
}
  }
}

@keyframes load {
  0% {
    stroke-dashoffset: 49;
    opacity: 1;
  }
  30% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
  35% {
    opacity: 0;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 0;
  }
}

@keyframes done {
  0% {
    transform: scale(0);
  }
  35% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  60% {
    opacity: 0;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeout {
  0% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes zoomin {
  0% {
    transform: scale(1);
  }
  60% {
    transform: scale(1);
  }
  90% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1.5);
  }
}

@keyframes zoomout {
  0% {
    transform: scale(1.5);
  }
  60% {
    transform: scale(1.5);
  }
  90% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
  }
}




.text-loading {
  transition: opacity 1s;
}

.text-loading.visible {
  opacity: 1;
}

.text-loading.d-none {
  opacity: 0;
  display: none;
}


.temporizador {
  margin-top: 20px;
  font-size: 1.2rem;
}

.paginator button {
  width: 35px;
  height: 35px;
  margin: 2px;
  font-size: 12px;
  background: transparent;
  border: none;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.paginator .answered {
  background-color: #add8e6;
}

.vertical-line {
  width: 1px;
  height: 50px;
}


.progress {
  height: 15px;
}

.progress-bar {
  background-color: #3569A5;
  transition: width 0.5s ease;
}


@media only screen and (min-width: 768px){
  .w-md-auto{
    width: max-content !important;
  }
}

.btnActive{
  background-color: #3569A5 !important;
  *{
    color: #fff !important;
  }
  transition: all .5s ease-in-out;
  -webkit-transition: all .5s ease-in-out;
  -moz-transition: all .5s ease-in-out;
  -ms-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
}
